import Code from './CodePanel/Code';
import Tabs from './Tabs';
import './CodePanel/Style.scss';

interface CodePanelProps {
  lang: {
    [name: string]: string
  },
  onInput?: (text: string) => void
}

export default function CodePanel({ lang, onInput = () => {} }: CodePanelProps) {
  return (
    <div className="panel language-markup">
      <Tabs tabs={Object.keys(lang)}>
        {Object.entries(lang).map(
          (entry, key) => <Code content={entry[1]} key={key} onInput={onInput} />
        )}
      </Tabs>
    </div>
  );
};